import React, { useState, useEffect } from "react";
import Info from "../info";
import axios from "axios";
import Movie from "../components/Movie";
import { type } from "language-tags";
import Button from "@material-ui/core/Button";
import './Home.css'

const Counter = () => {
  const [value, setValue] = useState(0)
  return(
    <div>
      <p>
        현재 카운터 값은 <b>{value}</b> 입니다.
      </p>
      <Button variant='contained' color='primary' onClick={()=>setValue(value + 1)}>+1</Button>
      <Button variant='contained' color='secondary' onClick={()=>setValue(value - 1)}>-1</Button>
    </div>
  )
}
const Home = () => {
  const [isLoading, setLoading] = useState(true);
  const [movies, setMovies] = useState([]);

  useEffect(() => {
    console.log("updated");
    getMovies(setMovies, setLoading);
  }, []);

  // setLoading(true)

  return (
    <section className='container'>

      {isLoading
        ? (
          <div className='loader'>
          <span className='loader__text'>Loading...</span>
          </div>
        ): (
          <div className='movies'>
          {movies.map((movie) => {
            console.log(movie);
            return (
              <Movie key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} genres={movie.genres}/>
            );
          })}
          </div>
          )
        }
      {/* {isLoading ? "Loading..." : 'alpha'} */}
    </section>
  );
};

const getMovies = async (setMovies, setLoading) => {
  const movies = await axios.get(
    "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
  );
  console.log(movies.data.data.movies);
  setMovies(movies.data.data.movies);
  setLoading(false);

  // let dd = movies.data.data.movies
  // console.log(dd)
  // console.log(typeof(dd))
};

export default Home;
